@import url(https://fonts.googleapis.com/css2?family=Roboto+Slab:wght@200;300;400;600;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@200;300;400;500;700&display=swap);
.header{display:flex;align-items:center;justify-content:space-between;position:fixed;top:0;left:0;right:0;z-index:1000;padding:1em}@media screen and (min-width: 768px){.header{padding:1em 4em}}@media screen and (min-width: 1224px){.header{padding:1em 10em}}@media screen and (min-width: 1920px){.header{padding:1em 15em}}@media screen and (min-width: 2240px){.header{padding:1em 18em}}

.logo{color:#faf9f6;font-weight:600;font-family:"Roboto Slab",serif;text-decoration:none}.logo--sm{font-size:1rem}.logo--md{font-size:1.5rem}.logo--lg{font-size:2rem}

.hamburger{cursor:pointer;-webkit-animation:fade-in 1s cubic-bezier(0.215, 0.61, 0.355, 1);animation:fade-in 1s cubic-bezier(0.215, 0.61, 0.355, 1)}.hamburger span{display:block;background-color:#faf9f6;height:2px}.hamburger span:nth-child(2),.hamburger span:nth-child(3){transition:width .2s}.hamburger span:nth-child(1){width:25px}.hamburger span:nth-child(2){margin:6px 0;width:20px}.hamburger span:nth-child(3){width:15px}.hamburger:hover span:nth-child(2){width:22px}.hamburger:hover span:nth-child(3){width:17px}

.dropdown{display:flex;flex-direction:column;justify-content:center;position:fixed;top:0;left:0;right:0;background-color:#131313;overflow:hidden;z-index:0;height:0;transition:height .7s ease-in-out}.dropdown_menu{list-style-type:none;padding:6em 1em}.dropdown_item{display:flex;align-items:center;justify-content:space-between;margin-bottom:1.5em}.dropdown_link{color:#faf9f6;font-size:2rem;font-family:"Roboto Slab",serif;text-decoration:none}.dropdown_link:hover{color:#3466aa}@media screen and (min-width: 768px){.dropdown_menu{padding:1em 4em}}@media screen and (min-width: 1224px){.dropdown_menu{padding:1em 10em}}@media screen and (min-width: 1920px){.dropdown_menu{padding:1em 15em}.dropdown_link{font-size:2.5rem}}@media screen and (min-width: 2240px){.dropdown_menu{padding:1em 20em}}@media screen and (min-width: 3240px){.dropdown_menu{padding:1em 22em}}

.hero{display:flex;flex-direction:column;justify-content:center;height:100vh;padding:0 2em}.hero_eyebrow,.hero_title,.hero_subtitle,.hero_link{-webkit-animation:slide-in 0.7s cubic-bezier(0.215, 0.61, 0.355, 1) 0.25s backwards;animation:slide-in 0.7s cubic-bezier(0.215, 0.61, 0.355, 1) 0.25s backwards}.hero_eyebrow,.hero_title{font-family:"Roboto Slab",serif;margin-bottom:.5em}.hero_eyebrow{font-size:1.5rem;font-weight:200}.hero_title{font-size:2.5rem}.hero_subtitle{color:#838383;font-weight:400;font-size:1rem;margin-bottom:1em}.hero_link{color:#3466aa;font-size:1rem;text-decoration:none}@media screen and (min-width: 1280px){.hero_eyebrow{font-size:2.5rem}.hero_title{font-size:3.5rem}.hero_subtitle{font-size:1.5rem}.hero_link{font-size:1.5rem}}@media screen and (min-width: 1920px){.hero_title{font-size:4.5rem}}

.section{display:flex;justify-content:center;flex-direction:column;height:100vh;padding:0 2em}.section_title{font-size:2rem;font-family:"Roboto Slab",serif;margin-bottom:.5em}.section_text{font-size:18px;line-height:1.5em;margin-bottom:1em}.section_link{display:block;color:#3466aa;font-size:18px;font-weight:500;text-decoration:none;margin-bottom:1em}.section_link:hover{text-decoration:underline}@media screen and (min-width: 768px){.section--grid{display:grid;grid-template-columns:1fr 2fr;grid-template-rows:2fr 1fr;grid-gap:2em;gap:2em}.section--grid .card:nth-child(1){grid-column:1 / 2;grid-row:1 / 3}}

.card{display:block;cursor:pointer;margin-bottom:1em}.card_img{border-radius:5px;-webkit-filter:brightness(50%);filter:brightness(50%);height:100%;width:100%;object-fit:cover}.card_name{font-size:1.5rem;font-family:"Roboto Slab",serif}

*,*::before,*::after{box-sizing:border-box;margin:0;padding:0}html{scroll-behavior:smooth}body{background-color:#131313;color:#faf9f6;font:400 calc(1em + 1vw) "Montserrat",sans-serif}h1,h2,h3{display:block;margin-bottom:.5em}p{font-size:18px;line-height:1.5em;margin-bottom:1.5em}img{max-width:100%;max-height:100%}.max-width{max-width:25.1em;margin:0 auto}.dot{display:inline-block;color:#3466aa;margin-left:2px}.wave,.scratch{display:inline-block;position:relative;z-index:-1}.wave{-webkit-transform-origin:70% 70%;transform-origin:70% 70%;-webkit-animation:wave 2s infinite;animation:wave 2s infinite}.scratch{-webkit-animation:scratch 2s infinite;animation:scratch 2s infinite}@-webkit-keyframes wave{0%,100%{-webkit-transform:rotate(0deg);transform:rotate(0deg)}10%,30%,50%{-webkit-transform:rotate(10deg);transform:rotate(10deg)}20%,40%,60%{-webkit-transform:rotate(-8deg);transform:rotate(-8deg)}}@keyframes wave{0%,100%{-webkit-transform:rotate(0deg);transform:rotate(0deg)}10%,30%,50%{-webkit-transform:rotate(10deg);transform:rotate(10deg)}20%,40%,60%{-webkit-transform:rotate(-8deg);transform:rotate(-8deg)}}@-webkit-keyframes scratch{0%,100%{-webkit-transform:translate(0);transform:translate(0)}10%,30%,50%{-webkit-transform:translate(1.5px, 1.5px);transform:translate(1.5px, 1.5px)}20%,40%,60%{-webkit-transform:translate(-1.5px, -1.5px);transform:translate(-1.5px, -1.5px)}}@keyframes scratch{0%,100%{-webkit-transform:translate(0);transform:translate(0)}10%,30%,50%{-webkit-transform:translate(1.5px, 1.5px);transform:translate(1.5px, 1.5px)}20%,40%,60%{-webkit-transform:translate(-1.5px, -1.5px);transform:translate(-1.5px, -1.5px)}}@-webkit-keyframes slide-in{from{opacity:0;-webkit-transform:translateY(150%);transform:translateY(150%)}to{opacity:1;-webkit-transform:translateY(0);transform:translateY(0)}}@keyframes slide-in{from{opacity:0;-webkit-transform:translateY(150%);transform:translateY(150%)}to{opacity:1;-webkit-transform:translateY(0);transform:translateY(0)}}

